import 'react-native';
import React from 'react';
import {render, screen, fireEvent} from '@testing-library/react-native';
import FloatingButton from '../FloatingButton';
import {shallow} from 'enzyme';

describe('Floating Button', () => {
  it('renders correctly on Detail Screen', () => {
    const {getByText, getByTestId} = render(<FloatingButton />);
    expect(getByTestId('add')).toBeTruthy();
  });

  // it('calls onPress prop when menu icon is pressed', () => {
  //     const onPress = jest.fn();
  //     const {getByTestId} = render(<FloatingButton onPress={onPress} />);
  //     fireEvent.press(getByTestId('add'));
  //     expect(onPress).toHaveBeenCalled();
  //     expect(props.navigation.navigate).toHaveBeenCalledWith('Detail', {item: data , screen :'Add Item'});
  //   });

  it('calls navigation.navigate when pressed', () => {
    const data = {
      id: 1,
      name: 'Test',
      description: 'here is description',
      date: new Date(),
    };
    const navigation = {navigate: jest.fn()};
    const onPress = jest.fn();
    const {getByTestId} = render(
      <FloatingButton
        navigation={navigation}
        onPress={() =>
          navigation.navigate('Detail', {item: data, screen: 'Add Item'})
        }
      />,
    );
    const button = getByTestId('floating-button');
    fireEvent.press(button);
    expect(navigation.navigate).toHaveBeenCalledWith('Detail', {
      item: data,
      screen: 'Add Item',
    });

    // expect(navigation.navigate).toHaveBeenCalledWith('Detail', {
    //   item: data,
    //   screen: 'Add Item',
    // });
  });
});
